.form-con{--s:25px;--g:20px;--f:1.0;display:grid;width:100%;gap:var(--g);grid-template-columns:repeat(2,auto)}.email-form input,.name-person input,.phone-form input{margin:5px;width:350px;padding:15px;border:0;border-radius:10px}.event-form select,.findus-form select{margin:5px;width:350px;padding:15px;border:0;border-radius:10px}.textbox textarea{width:350px;border-radius:15px;border:0;padding:20px;height:160px}.form-section-con{position:relative;padding:45px;display:flex;justify-content:center;border-radius:20px;margin:auto;width:80%;height:auto;background-color:#e8dcc68a}label{font-weight:500;font-size:15px}/* Modal Styles */
.modal-content {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
}

.modal-header {
  border-bottom: none;
}

.modal-title {
  color: #333;
  font-size: 1.5rem;
  font-weight: bold;
}

.modal-body {
  color: #555;
  font-size: 1rem;
}

.modal-footer {
  border-top: none;
}

/* Close Button */
.modal-footer .btn-secondary {
  background-color: #f0f0f0;
  color: #555;
  border-radius: 5px;
}

.modal-footer .btn-secondary:hover {
  background-color: #e0e0e0;
}

/* Form Styles */
.form-con {
  display: flex;
  justify-content: space-between;
}

.left-form-area,
.right-form-area {
  flex: 1;
}

label {
  font-weight: bold;
  color: #333;
}

input,
select,
textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.submit-btn {
  background-color: var(--golden);
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  transition: .2s ease;
}

.submit-btn:hover {
    color: var(--golden);
  background-color: var(--white);
}
