@font-face {
  font-family:Editors;
  src:url(./fonts/EditorsNote-Regular.otf)
  }
  
  @font-face {
  font-family:Juliette;
  src:url(./fonts/Juliette.otf)
  }
  
  @font-face {
    font-family:Sattin;
    src:url(./fonts/ActerumSignaturePersonalUse.ttf)
    }

  @font-face {
    font-family:Poppins;
    src:url(./fonts/Poppins-Regular.ttf)
    }
    @font-face {
      font-family:Inter;
      src:url(./fonts/Inter-Regular.ttf)
      }
    
  * {
  margin:0;
  padding:0;
  box-sizing:border-box
  }
  
  .load-more-button {
  z-index:10;
  margin:auto;
  box-shadow:#0000001a 0 4px 12px;
  width:15%;
  padding-block:17px;
  padding-inline:12px;
  border:none;
  cursor:pointer;
  background-color:var(--black-nav);
  color:var(--white);
  letter-spacing:2px;
  transition:.3s ease
  }
  
  .load-more-button:hover {
  box-shadow:none;
  background-color:var(--white);
  color:var(--golden);
  font-weight:800;
  letter-spacing:4px
  }
  
  .load-more-button-blog {
  z-index:10;
  margin-left:42.5%;
  margin-right:42.5%;
  box-shadow:#0000001a 0 4px 12px;
  width:15%;
  padding-block:17px;
  padding-inline:12px;
  border:none;
  cursor:pointer;
  background-color:var(--black-nav);
  color:var(--white);
  letter-spacing:2px;
  transition:.3s ease
  }
  
  .load-more-button-blog:hover {
  box-shadow:none;
  background-color:var(--white);
  color:var(--golden);
  font-weight:800;
  letter-spacing:4px
  }
  
  @media screen and (max-width: 767px) {
  .load-more-button-blog {
  z-index:10;
  margin-left:20%;
  margin-right:20%;
  box-shadow:#0000001a 0 4px 12px;
  width:60%;
  padding-block:17px;
  padding-inline:12px;
  border:none;
  cursor:pointer;
  background-color:var(--black-nav);
  color:var(--white);
  letter-spacing:2px;
  transition:.3s ease
  }
  
  .load-more-button {
  z-index:10;
  margin:auto;
  box-shadow:#0000001a 0 4px 12px;
  width:60%;
  padding-block:17px;
  padding-inline:12px;
  border:none;
  cursor:pointer;
  background-color:var(--black-nav);
  color:var(--white);
  letter-spacing:2px;
  transition:.3s ease
  }
  }
  
.Upiconbtn {
  text-decoration:none;
  -webkit-transition:all .3s linear;
  -moz-transition:all .3s ease;
  -ms-transition:all .3s ease;
  -o-transition:all .3s ease;
  transition:all .3s ease;
  position: relative;
  color: var(--white);
}

.Upiconbtn:hover {
  margin-bottom: 4px;
}

  #return-to-top {
  outline:none;
  border:none;
  cursor:pointer;
  z-index:500;
  position:fixed;
  bottom:34px;
  right:20px;
  background:var(--golden);
  width:50px;
  height:50px;

  }
  

  
  #return-to-top:hover {
  background:#000000e6
  }
  
