  }
  
  @font-face {
  font-family:Juliette;
  src:url(./fonts/Juliette.otf)
  }
  
  @font-face {
    font-family:Sattin;
    src:url(./fonts/ActerumSignaturePersonalUse.ttf)
    }
  @font-face {
    font-family:Poppins;
    src:url(./fonts/Poppins-Regular.ttf)
    }
    @font-face {
      font-family:Inter;
      src:url(./fonts/Inter-Regular.ttf)
      }
:root {
  --story-back:#f4e8d282;
  --white:#fff;
  --golden:#BA916B;
  --animate-duration: 2000ms;
  --animate-delay: 4s;
  --black:#222222;
  --black-nav:#121212;
  --body-color:#f0ede9;
}



body {
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  background: var(--body-color);
overflow: overlay;
font-family: Inter;
}



* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}



::-moz-selection {
  color: var(--white);
  background: #000;
}

::selection {
  color: var(--white);
  background: #000;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: none;
}
::-webkit-scrollbar {
  width: 5px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: var(--black);
}


